html {
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
}

main {
  display: block;
}

h1 {
  margin: .67em 0;
  font-size: 2em;
}

hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible;
}

pre {
  font-family: monospace;
  font-size: 1em;
}

a {
  background-color: #0000;
}

abbr[title] {
  border-bottom: none;
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp {
  font-family: monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

img {
  border-style: none;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, input {
  overflow: visible;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

button::-moz-focus-inner, [type="button"]::-moz-focus-inner, [type="reset"]::-moz-focus-inner, [type="submit"]::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

button:-moz-focusring, [type="button"]:-moz-focusring, [type="reset"]:-moz-focusring, [type="submit"]:-moz-focusring {
  outline: 1px dotted ButtonText;
}

fieldset {
  padding: .35em .75em .625em;
}

legend {
  box-sizing: border-box;
  color: inherit;
  max-width: 100%;
  white-space: normal;
  padding: 0;
  display: table;
}

progress {
  vertical-align: baseline;
}

textarea {
  overflow: auto;
}

[type="checkbox"], [type="radio"] {
  box-sizing: border-box;
  padding: 0;
}

[type="number"]::-webkit-inner-spin-button, [type="number"]::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

details {
  display: block;
}

summary {
  display: list-item;
}

template, [hidden] {
  display: none;
}

html, body {
  box-sizing: border-box;
}

html *, html :after, html :before, body *, body :after, body :before {
  box-sizing: inherit;
}

img {
  max-width: 100%;
}

.info {
  width: auto;
  background: #fff;
  padding: 3px 5px;
  font-size: .9rem;
  position: absolute;
  bottom: 0;
  left: 0;
}

.info.top {
  top: 0;
  bottom: auto;
}

canvas {
  display: block;
}

@font-face {
  font-family: Manrope;
  src: url("../Manrope[wght].6e8cea27.ttf") format("truetype-variations");
}

html {
  font-family: Manrope, sans-serif;
  line-height: 1.6;
}

h1, h2, h3, h4, h5, p, ul, ol {
  margin: 0 0 .5rem;
  transition: all .2s;
}

h1, .h1 {
  font-size: 1.6rem;
  font-weight: 300;
}

h2, .h2 {
  font-size: 1.4rem;
  font-weight: 300;
}

h3, .h3 {
  font-size: 1.2rem;
  font-weight: 300;
}

h4, .h4 {
  font-size: 1rem;
  font-weight: 500;
}

h5, .h5 {
  text-transform: uppercase;
  font-size: .64rem;
}

button, .btn {
  color: #a9acbd;
  cursor: pointer;
  background-color: #2d2d39;
  border: none;
  margin: 0;
  padding: 4px;
  font-size: .8rem;
  font-weight: 500;
  transition: all .3s;
}

button:hover, button:focus, .btn:hover, .btn:focus {
  background-color: #252541;
  font-weight: 700;
}

button:focus, .btn:focus {
  outline: 2px solid #32de8a;
}

button:focus:not(:focus-visible), .btn:focus:not(:focus-visible) {
  outline: none;
}

button:focus-visible, .btn:focus-visible {
  outline: 2px solid #32de8a;
}

table {
  border-collapse: collapse;
}

table th[scope="row"] {
  text-align: left;
}

table tr:nth-child(2n-1) {
  background-color: #edeef6;
}

table.main {
  max-width: 900px;
  margin: 50px 5%;
}

.gallery {
  grid-template-columns: 1fr 1fr;
  display: grid;
  position: relative;
}

@media (width >= 850px) {
  .gallery {
    grid-template-columns: 1fr 1fr 1fr;
  }
}

@media (width >= 1000px) {
  .gallery {
    grid-template-columns: repeat(4, 1fr);
  }
}

.gallery-item {
  padding: 15%;
  overflow: hidden;
}

.gallery-item.info-item {
  background-color: #e8ff52 !important;
}

.gallery-item.info-item a {
  color: #e05a00;
}

.gallery-item.info-item a:hover, .gallery-item.info-item a:focus {
  color: #8500aa;
}

.gallery-item:first-child {
  background-color: #fd492d;
}

.gallery-item:nth-child(2) {
  background-color: #501f37;
}

.gallery-item:nth-child(3) {
  background-color: #524cb3;
}

.gallery-item:nth-child(4) {
  background-color: #83e0dc;
}

.gallery-item:nth-child(5) {
  background-color: #19224e;
}

.gallery-item:nth-child(6) {
  background-color: #0a848a;
}

.gallery-item:nth-child(7) {
  background-color: #ff1904;
}

.gallery-item:nth-child(8) {
  background-color: #10a8aa;
}

.gallery-item:nth-child(9) {
  background-color: #c92dec;
}

.gallery-item:nth-child(10) {
  background-color: #62ff37;
}

.gallery-item:nth-child(11) {
  background-color: #810de0;
}

.gallery-item:nth-child(12) {
  background-color: #f2e724;
}

.gallery-item:nth-child(13) {
  background-color: #fa7122;
}

.gallery-item:nth-child(14) {
  background-color: #32f2b5;
}

.gallery-item:nth-child(15) {
  background-color: #f22424;
}

.gallery-img-wrap {
  width: 100%;
  padding-bottom: 100%;
  position: relative;
}

.gallery-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
  z-index: 1;
  display: block;
  position: absolute;
}

.gallery-img.gallery-img-extra {
  filter: blur(20px) saturate(250%) brightness(150%);
  z-index: 0;
  opacity: 0;
  will-change: opacity;
  transition: all .2s;
  transform: scale(1.05);
}

.gallery-title {
  z-index: 1;
  -webkit-backdrop-filter: blur(2px);
  backdrop-filter: blur(2px);
  width: 100%;
  text-align: right;
  opacity: 0;
  background-color: #00000080;
  padding: .2rem 1rem;
  transition-property: transform, opacity;
  transition-duration: .2s;
  position: absolute;
  bottom: 0;
  left: 0;
}

.gallery-title-text {
  opacity: 0;
  transition: all .2s ease-in-out;
  display: block;
  transform: translate(50%);
}

.gallery-link {
  color: #fff;
  display: block;
  position: relative;
}

.gallery-link:hover .gallery-img-extra, .gallery-link:focus .gallery-img-extra {
  opacity: 1;
}

#pt {
  position: absolute;
  inset: 0;
}

.taglist {
  display: flex;
}

.taglist span {
  margin-right: 1rem;
}

