button,
.btn {
	background-color: $dark2;
	border: none;
	color: $lightgray;
	margin: 0;
	cursor: pointer;
	padding: 4px 4px;
	font-size: 0.8rem;
	font-weight: 500;
	transition: all 300ms;
	&:hover,
	&:focus {
		background-color: $cadet;
		font-weight: 700;
	}

	&:focus {
		outline: 2px solid $spring;
	}
	&:focus:not(:focus-visible) {
		outline: none;
	}
	&:focus-visible {
		outline: 2px solid $spring;
	}
}
